.Sidebar{
    height: 100%;
    width: 300px;
    background-color: #2F4050;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    /* width: 200px; */
    height: 100%;
}

.SidebarList{
    height: auto;
    width: 100%;
    padding: 0;    
}
.SidebarList .row{
    width: 100%;
    height: 60px;
    list-style-type: none;
    margin: 0%;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 28px;
    padding-bottom: 10px;
    font-family: 'Times New Roman', Times, serif;
}

.row #title{
    flex:70%
}

.SidebarList .row:hover{
    cursor: pointer;
    background-color: #293846;
}

.SidebarList  #active{
    background-color: #293846;
}
.main-content {
    margin-left: 300px;
  }
.btnbg{
    background-color: yellow;
}